*,
*:after,
*:before {
    -khtml-user-select: none;
    -webkit-user-drag: none;
    -moz-user-select: none;
    -ms-user-drag: none;
    -khtml-user-drag: none;
    -o-user-drag: none;
    -moz-user-drag: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
}

html,
body {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

body {
    background-color: rgb(63,94,251);
    background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(62,56,169,1) 42%, rgba(61,36,126,1) 70%, rgba(60,16,83,1) 100%);
    background: -moz-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(62,56,169,1) 42%, rgba(61,36,126,1) 70%, rgba(60,16,83,1) 100%);
    background: -webkit-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(62,56,169,1) 42%, rgba(61,36,126,1) 70%, rgba(60,16,83,1) 100%);
    background: -o-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(62,56,169,1) 42%, rgba(61,36,126,1) 70%, rgba(60,16,83,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#3f5efb",endColorstr="#3c1053",GradientType=1);
    background: url(./bg.jpg) no-repeat center center fixed;
    background-size: cover;
    background-color: transparent;
}

#gameContainer {
    display: block;
    position: relative;
    padding: 0;
    width: 100%;
    height: 100%;
    margin: 0;
}

.loader {
    position: absolute;
    top: calc(50% - 32px);
    left: calc(50% - 32px);
    width: 64px;
    height: 64px;
    border-radius: 50%;
    perspective: 800px;
}

.loaded {
    display: none;
}

.inner {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border-radius: 50%;  
}

.inner.one {
    left: 0%;
    top: 0%;
    animation: rotate-one 1s linear infinite;
    border-bottom: 3px solid #EFEFFA;
}

.inner.two {
    right: 0%;
    top: 0%;
    animation: rotate-two 1s linear infinite;
    border-right: 3px solid #EFEFFA;
}

.inner.three {
    right: 0%;
    bottom: 0%;
    animation: rotate-three 1s linear infinite;
    border-top: 3px solid #EFEFFA;
}

@keyframes rotate-one {
    0% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
    }
    100% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    }
}

@keyframes rotate-two {
    0% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
    }
    100% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    }
}

@keyframes rotate-three {
    0% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
    }
    100% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    }
}